/* ------------------- Start Global Rules ------------------*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

body::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
body::-webkit-scrollbar-track {
  background-color: $secondary-clr;
}
body::-webkit-scrollbar-thumb {
  background-color: $main-clr;
  border-radius: 10px;
}
body::-webkit-scrollbar-corner {
  background-color: $main-clr;
  border-radius: 10px;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;

  // -- tablet screens -- //
  @include breakpoint(tablet) {
    width: 750px;
  }

  // -- laptop screens -- //
  @include breakpoint(laptop) {
    width: 970px;
  }

  // -- desktop screens -- //
  @include breakpoint(desktop) {
    width: 1170px;
  }

  // -- tv screens -- //
  @include breakpoint(tv) {
    width: 1370px;
  }
}

video {
  max-width: 100%;
  height: auto;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
}
/* ------------ End Global Rules --------------*/
