// Here you can create and customize the pages' header (Navigation):

@use "../globals" as *;
@use "../helpers" as *;

%header {
  position: absolute;
  width: 100%;
  z-index: 2;
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    min-height: 97px;
    &::after {
      content: "";
      position: absolute;
      height: 1px;
      width: 97%;
      right: 0;
      left: 0;
      bottom: 0;
      margin: 0 auto;
      background-color: $text-clr;
    }
  }
  nav {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 30px;
  }
  .nav_items {
    display: none;
    flex-direction: column;
    text-align: center;
    position: absolute;
    left: 0;
    top: 100%;
    @include breakpoint(tablet) {
      display: flex !important;
      flex-direction: row;
      position: static;
    }
    a {
      display: block;
      font-size: 14px;
      padding: 3px 0;
      padding: 5px 0;
      color: $white;
      transition: $transition;
      @include breakpoint(mobile) {
        width: 100vw;
        border-radius: 5px;
        border-bottom: 2px solid $black;
        background-color: #107590;
        &:hover,
        &.active {
          font-weight: bold;
          color: $main-clr;
          background-color: #162d2d;
        }
      }
      @include breakpoint(tablet) {
        min-width: 80px;
        padding: 37px 10px;
        position: relative;
        z-index: 2;
        &:hover,
        &.active {
          color: $main-clr;
          border-bottom: 1px solid $main-clr;
        }
      }
    }
  }
  .search_form {
    display: flex;
    align-items: center;
    justify-content: center;
    color: $white;
    border-left: 1px solid $white;
    i {
      cursor: pointer;
      font-size: 20px;
      padding: 5px 0 5px 20px;
      transition: $transition;
      &:hover {
        color: $main-clr;
        transform: scale($scale);
      }
    }
  }
  .toggle_menu {
    width: 20px;
    @include breakpoint(tablet) {
      display: none;
    }
    cursor: pointer;
    span {
      display: block;
      width: 100%;
      height: 3px;
      margin-bottom: 3px;
      transition: $transition;
      background-color: $white;
    }
  }
}
