// Here You can style and customize the home page:

@use "../globals" as *;
@use "../mixins" as *;
@use "../helpers" as *;
@use "../components" as *;

// <> How to organize the CSS properties:
// 1 - >> display stuff:
// 2 - >> positioning stuff:
// 3 - >> box-model stuff:
// 4 - >> typography:
// 5 - >> manipulation:
// 6 - >> misc:

// --------------- start header Section ------------------//
header {
  @extend %header;
}
/* ---------------- End header Section ------------------*/

/* --------------- Start Slider Section -----------------*/
.slider {
  background: lightblue;
  background-image: url("../imgs/landing.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  position: relative;
  .overlay {
    @extend %overlay;
  }
  .text {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 25px;
    color: $white;
    background-color: $transparent-main-clr;
    @include breakpoint(tablet) {
      width: 50%;
      border-radius: 0 5px 5px 0;
    }
    @include breakpoint(desktop) {
      padding-left: 130px;
    }
    h2 {
      font-size: rem(32);
      line-height: 1.5;
      font-weight: normal;
      text-transform: capitalize;
    }
    p {
      font-size: 14px;
      line-height: 2;
    }
  }
  .arrow {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    @include breakpoint(mobile) {
      display: none;
    }
    i {
      position: relative;
      font-size: 30px;
      margin: auto 30px;
      cursor: pointer;
      color: $white;
      transition: $transition;
      &:hover {
        transform: scale($scale);
        color: $main-clr;
      }
    }
  }
  .bullets {
    display: flex;
    justify-content: space-between;
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    li {
      height: 20px;
      width: 20px;
      border: 1px solid $white;
      border-radius: 50%;
      cursor: pointer;
      transition: $transition;
    }
    li:hover,
    li.active {
      border-color: $main-clr;
      background-color: $main-clr;
    }
  }
}
/* ---------------- End Slider Section ------------------*/

/* -------------- Start Services Section ----------------*/
.services {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  .section_header {
    @extend %section_header;
  }
  .our_services {
    text-align: center;
    @include breakpoint(tablet) {
      text-align: unset;
    }
    @include breakpoint(laptop) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 50px;
    }
    .srv_box {
      margin-bottom: $txt-margin-md;
      @include breakpoint(tablet) {
        display: flex;
        column-gap: 50px;
      }
    }
    i {
      margin-bottom: $txt-margin;
    }
    h3 {
      margin-bottom: $txt-margin;
      color: $main-clr;
    }
    p {
      color: $txt-clr;
      line-height: $line-height;
    }
  }
}
/* --------------- End Services Section ----------------*/

/* --------------- Start Desgin Section ----------------*/
.design {
  background-color: lightblue;
  background-image: url("../imgs/design-features.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 600px;
  overflow: hidden;
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  &::after {
    @extend %overlay;
  }
  .image {
    flex: 1;
    text-align: center;
    @include breakpoint(mobile) {
      display: none;
    }
    img {
      position: relative;
      z-index: 2;
      bottom: -165px;
    }
  }
  .text {
    flex: 1;
    z-index: 2;
    width: 100%;
    color: $white;
    padding: $txt-padding-md;
    background-color: $transparent-main-clr;
    @include breakpoint(tablet) {
      width: unset;
      border-radius: 5px 0 0 5px;
    }
    h2 {
      font-size: 25px;
      font-weight: normal;
      text-transform: uppercase;
      margin-bottom: $txt-margin-md;
    }
    li {
      text-transform: capitalize;
      margin-bottom: $txt-margin;
      &::before {
        content: "\f108";
        font-family: "Font Awesome 5 Free";
        margin-right: $txt-margin;
        font-weight: 900;
      }
    }
  }
}

/* ---------------- End Desgin Section -----------------*/

/* -------------- Start portfolio Section --------------*/
.portfolio {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  .section_header {
    @extend %section_header;
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    li {
      cursor: pointer;
      padding: 10px;
      margin: 0 5px;
      border-radius: $border-radius;
      text-transform: capitalize;
      transition: $transition;
      &:hover,
      &.active {
        color: $white;
        background-color: $main-clr;
      }
    }
  }
}
/* -------------- End portfolio Section --------------*/

/* --------------- Start Gellary Section ---------------*/
.content {
  @include breakpoint(tablet) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  @include breakpoint(desktop) {
    grid-template-columns: repeat(4, 1fr);
  }
  figure {
    position: relative;
    overflow: hidden;
    &:hover figcaption {
      bottom: 0;
    }
    &:hover img {
      filter: grayscale(0);
      transform: rotate(3deg) scale(1.1);
    }
  }
  img {
    filter: grayscale(100%);
    transition: $transition;
  }
  figcaption {
    position: absolute;
    bottom: -100px;
    left: 0;
    width: 100%;
    padding: $txt-padding;
    transition: $transition;
    background-color: $white;
    h4 {
      text-transform: capitalize;
      font-weight: normal;
      margin-bottom: 10px;
    }
    p {
      text-transform: capitalize;
      color: $main-clr;
    }
  }
}
.gallary a {
  display: block;
  text-transform: uppercase;
  margin: 0 auto;
  width: fit-content;
  margin-top: $txt-margin;
  padding: 10px 15px;
  cursor: pointer;
  transition: $transition;
  color: $white;
  border-radius: $border-radius;
  background: $main-clr;
  &:hover {
    background-color: $main-clr-heavy;
  }
}

/* ---------------- End Gellary Section ----------------*/

/* ---------------- Start Video Section ----------------*/
.video {
  margin-top: $section-padding;
  position: relative;
  &::after {
    @extend %overlay;
    height: calc(100% - 5px);
  }
  .text {
    position: absolute;
    z-index: 2;
    text-align: center;
    width: 100%;
    top: 50%;
    padding: $txt-padding-md;
    transform: translateY(-50%);
    color: $white;
    background-color: #107590ad;
    @include breakpoint(small) {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      height: 100%;
      padding: $txt-padding;
    }
    h2 {
      font-weight: normal;
      text-transform: uppercase;
    }
    p {
      text-transform: capitalize;
      margin-bottom: $txt-margin;
    }
    button {
      text-transform: uppercase;
      padding: 10px 15px;
      cursor: pointer;
      color: $white;
      background-color: $black;
      border-radius: $border-radius;
    }
  }
}
/* ----------------- End Video Section -----------------*/

/* ---------------- Start About Section ----------------*/
.about {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  .section_header {
    @extend %section_header;
  }
  .img-holder {
    text-align: center;
    position: relative;
    bottom: -45px;
    @include breakpoint(tablet) {
      bottom: -95px;
    }
  }
  .stats {
    background-color: lightblue;
    background-image: url("../imgs/stats.png");
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    padding: $txt-padding-lg $txt-padding;
    &::after {
      @extend %overlay;
    }
    .content {
      display: grid;
      row-gap: $txt-margin-lg;
      position: relative;
      z-index: 2;
      border-radius: $border-radius;
      padding: $txt-padding-md;
      background-color: #107590ab;
      @include breakpoint(laptop) {
        grid-template-columns: repeat(4, 1fr);
      }
      .box {
        text-align: center;
        color: $white;
        i {
          margin-bottom: $txt-margin;
          padding: 10px;
          border-radius: 50%;
          background-color: $black;
        }
        span:first-of-type {
          display: block;
          font-size: 50px;
          font-weight: bold;
        }
        span:last-of-type {
          font-size: 14px;
          text-transform: capitalize;
        }
      }
    }
  }
}

/* ----------------- End About Section -----------------*/

/* ----------------- Start info Section ----------------*/
.info {
  .container {
    text-align: center;
    @include breakpoint(laptop) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 100px;
    }
    .section_header {
      h3 {
        font-weight: normal;
        text-transform: uppercase;
        margin: 0 0 30px;
      }
      p {
        line-height: $line-height;
        margin-bottom: $txt-margin-md;
        color: $txt-clr;
      }
    }
    .testimonials {
      .box {
        margin-bottom: $txt-margin-md;
        @include breakpoint(tablet) {
          display: flex;
          justify-content: space-between;
          align-items: center;
        }
        img {
          width: 100px;
          margin: 0 auto 20px;
          border-radius: 50%;
          @include breakpoint(tablet) {
            margin-right: 50px;
          }
        }
        .text {
          border-bottom: 1px solid $text-clr;
          @include breakpoint(tablet) {
            text-align: start;
          }
          p {
            line-height: $line-height;
          }
          span {
            display: block;
            text-align: end;
            font-size: 14px;
            text-transform: capitalize;
            margin: 5px;
            color: $txt-clr;
          }
        }
      }
      ul {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100px;
        width: 65px;
        margin: 0 auto $txt-margin-lg;
        li {
          height: 15px;
          width: 15px;
          cursor: pointer;
          border: 1px solid $txt-clr;
          border-radius: 50%;
          transition: $transition;
          &:hover,
          &.active {
            border-color: $main-clr;
            background-color: $main-clr;
          }
        }
      }
    }
    .skills {
      .prog-holder {
        text-align: start;
        margin-bottom: 40px;
        h4 {
          font-weight: normal;
          text-transform: uppercase;
          margin-bottom: 15px;
        }
        .prog {
          background-color: $text-clr;
          height: 30px;
          border-radius: 5px;
          overflow: hidden;
          span {
            display: block;
            height: 100%;
            position: relative;
            background-color: $main-clr;
            &::before {
              content: attr(data-progress);
              display: flex;
              justify-content: center;
              align-items: center;
              position: absolute;
              top: -35px;
              right: -18px;
              height: 25px;
              width: 40px;
              border-radius: 5px;
              color: $white;
              background-color: $black;
            }
            &::after {
              content: "";
              display: flex;
              position: absolute;
              top: -10px;
              right: -5px;
              border-width: 7px;
              border-style: solid;
              border-color: black transparent transparent transparent;
            }
          }
        }
      }
    }
  }
}
/* ------------------ End info Section -----------------*/

/* ----------------- Start Quote Section ---------------*/
.quote {
  background-color: lightblue;
  background-image: url("../imgs/quote.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  text-align: center;
  color: white;
  padding: $txt-padding-md 10px;
  @include breakpoint(tablet) {
    padding: $section-padding $txt-padding-md;
  }
  &::after {
    @extend %overlay;
  }
  .text {
    position: relative;
    z-index: 2;
    max-width: 1000px;
    margin: 0 auto;
    q {
      display: block;
      font-size: 30px;
      margin-bottom: $txt-margin;
    }
    span {
      font-size: 14px;
      text-transform: capitalize;
    }
  }
}

/* ------------------ End Quote Section ----------------*/

/* ---------------- Start Pricing Section --------------*/
.pricing {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  .section_header {
    @extend %section_header;
  }
  .offers {
    @include breakpoint(tablet) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 30px;
    }
    @include breakpoint(desktop) {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
    }
    .offer {
      padding-bottom: 40px;
      margin-bottom: 40px;
      text-align: center;
      background-color: #fcfcfc;
      .header {
        padding: 35px 0;
        border-top: 1px solid $main-clr;
        border-bottom: 1px solid $main-clr;
        .level {
          font-size: 20px;
          text-transform: uppercase;
        }
        .price {
          font-size: 60px;
          font-weight: bold;
          sup {
            position: relative;
            top: -30px;
            font-size: 25px;
            font-weight: normal;
            margin-right: 15px;
          }
          sub {
            text-transform: capitalize;
            font-size: 20px;
          }
        }
      }
      ul {
        border-bottom: 1px solid $main-clr;
        text-transform: capitalize;
        li {
          width: 140px;
          margin: 0 auto;
          padding: 20px 0;
          &:not(:last-of-type) {
            border-bottom: 1px solid $main-clr;
          }
        }
      }
      a {
        display: block;
        width: fit-content;
        text-transform: capitalize;
        margin: 30px auto 0;
        padding: 15px 20px;
        border: 1px solid;
        color: $main-clr;
        transition: $transition;
        background-color: white;
        border-radius: $border-radius;
        &:hover {
          color: white;
          background-color: $main-clr;
        }
      }
    }
  }
  .text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    p {
      text-align: center;
      font-size: 20px;
      margin-bottom: 15px;
    }
    a {
      padding: 15px 30px;
      transition: $transition;
      color: white;
      border-radius: $border-radius;
      background-color: $main-clr;
      &:hover {
        background-color: $main-clr-heavy;
      }
    }
  }
}
/* ----------------- End Pricing Section ---------------*/

/* --------------- Start Subscribe section -------------*/
.subscribe {
  background-color: lightblue;
  background-image: url("../imgs/subscribe.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  position: relative;
  &::after {
    @extend %overlay;
  }
  .container {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    @include breakpoint(laptop) {
      flex-direction: row;
      align-items: start;
      column-gap: 50px;
    }
  }
  form {
    position: relative;
    height: 60px;
    margin-bottom: 20px;
    border: 2px solid wheat;
    border-radius: $border-radius;
    @media (min-width: 481px) {
      width: 435px;
    }
    i {
      position: absolute;
      color: $white;
      top: 50%;
      transform: translateY(-50%);
      left: 10px;
    }
    input[type="email"] {
      height: 100%;
      width: calc(100% - 105px);
      padding: 15px 15px 20px 40px;
      border: none;
      caret-color: $main-clr;
      color: white;
      background-color: transparent;
      &::placeholder {
        color: $text-clr;
      }
      &:focus {
        outline: none;
      }
    }
    input[type="submit"] {
      max-height: 100%;
      width: 100px;
      padding: 20px;
      border: none;
      cursor: pointer;
      transition: $transition;
      color: white;
      background-color: $main-clr;
      &:hover {
        background-color: $main-clr-heavy;
      }
    }
  }
  p {
    max-width: 435px;
    line-height: 2;
    text-align: center;
    color: white;
  }
}

/* ---------------- End Subscribe section --------------*/

/* ---------------- Start Contact section --------------*/
.contact {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  .section_header {
    @extend %section_header;
  }
  .content {
    @include breakpoint(tablet) {
      display: flex;
    }
    .info {
      flex-basis: 50%;
      order: 2;
      text-align: center;
      h4 {
        font-size: 18px;
        margin: 20px 0;
        font-weight: normal;
        text-transform: uppercase;
      }
      span,
      a {
        display: block;
        margin: 10px 0;
        text-transform: capitalize;
        color: $txt-clr;
      }
      .adress {
        @include breakpoint(tablet) {
          margin-top: 100px;
        }
      }
    }
    form {
      input:not(:last-of-type) {
        width: 100%;
        padding: 20px;
        margin: 20px 0;
        border-radius: $border-radius;
        &::placeholder {
          text-transform: capitalize;
        }
      }
      textarea {
        width: 100%;
        height: 200px;
        overflow: auto;
        resize: none;
        padding: 20px;
        margin: 20px 0;
        border-radius: $border-radius;
        text-transform: capitalize;
      }
    }
    input:last-of-type {
      display: flex;
      padding: 20px;
      text-transform: uppercase;
      border: none;
      margin-left: auto;
      color: white;
      cursor: pointer;
      border-radius: $border-radius;
      transition: $transition;
      background-color: $main-clr;
      &:hover {
        background-color: $main-clr-heavy;
      }
    }
  }
}

/* ----------------- End Contact section --------------*/

/* ---------------- Start Footer section --------------*/
footer {
  @extend %footer;
}

/* ---------------- Start Footer section --------------*/
