// Here you can create and customize the pages' footer:

@use "../globals" as *;
@use "../helpers" as *;
@use "../mixins" as *;

%footer {
  background-color: lightblue;
  background-image: url("../imgs/subscribe.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  padding-top: calc($section-padding / 2);
  padding-bottom: calc($section-padding / 2);
  position: relative;
  &::after {
    @extend %overlay;
  }
  .container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    z-index: 2;
    row-gap: 30px;
    color: white;
  }
  h2 {
    text-transform: uppercase;
    text-align: center;
    font-weight: normal;
    padding: 20px 0;
    border-bottom: 1px solid white;
  }
  p {
    text-transform: capitalize;
    margin-top: 10px;
    span:last-of-type {
      font-weight: bold;
      color: $main-clr;
    }
  }
  a {
    transition: $transition;
    margin: 10px 20px;
    cursor: pointer;
    color: white;
    &:hover {
      color: $main-clr-heavy;
    }
  }
  p:last-of-type {
    span:first-of-type {
      color: red;
    }
  }
}
