// Here you can create and customize the sections' header:

@use "../globals" as *;
@use "../helpers" as *;

%section_header {
  text-align: center;
  h2 {
    position: relative;
    font-weight: normal;
    font-size: rem(40);
    margin-bottom: $txt-margin-md;
    text-transform: uppercase;
    &::before {
      content: "";
      position: absolute;
      width: rem(120);
      height: rem(2);
      bottom: rem(-30);
      left: 50%;
      transform: translateX(-50%);
      background-color: #333;
    }
    &::after {
      content: "";
      position: absolute;
      bottom: rem(-38);
      height: rem(14);
      width: rem(14);
      left: 50%;
      transform: translateX(-50%);
      border-radius: 50%;
      background-color: #fff;
      border: 2px solid #333;
    }
  }
  p {
    line-height: $line-height;
    margin: 0 auto rem(75) auto;
    color: $txt_clr;
    max-width: 100%;
    width: rem(550);
    font-size: rem(16);
  }
}
